@import "react-datepicker/dist/react-datepicker.css";

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'inter', sans-serif;
}
header {
    display: flex;
    width: 100%;
    height: 90px;
    justify-content: space-between;
    align-items: center;
    padding: 0 130px;
    background-color: #ffffff;
}

/* Header Styles */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 24px;
    background-color: white;
    border-bottom: 1px solid #e2e8f0;
}

.header__logo img {
    height: 32px;
}

.header__user-dropdown select {
    background: none;
    border: none;
    font-size: 14px;
    color: #64748b;
    cursor: pointer;
    padding: 8px 12px;
    border-radius: 6px;
}

.header__user-dropdown select:hover {
    background-color: #f1f5f9;
}



header .login-register,
.sign-up {
    line-height: 40px;
}

/* Footer */
.footer {
    background: #1f2937;
    color: #9ca3af;
    text-align: center;
    padding: 20px;
    font-size: 14px;
}

.footer__text {
    margin: 0;
}